import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';
import { merge, tada, flip, headShake } from 'react-animations';
import qos_png from './qos.png';
import jod_png from './jod.png';
import './pinochle.css';

const TadaFlip = styled.div`font-size: 48px; animation: ${keyframes`${merge(tada, flip)}`} 5s infinite`;
const HeadShake = styled.div`font-size: 20px; animation: ${keyframes`${headShake}`} 5s infinite`;

class PinochleGame extends Component {

    start() {
        alert("Hello!");
    }

    render() {
        const CardQS = styled.img`height: 20vmin; animation: ${keyframes`from {transform: rotate(0deg);} to {transform: rotate(360deg);}`} infinite 3s alternate;`;
        const CardJD = styled.img`height: 20vmin; animation: ${keyframes`from {transform: rotate(360deg);} to {transform: rotate(0deg);}`} infinite 3s alternate;`;
        return (
            <div>
                <TadaFlip>Nickster Cards</TadaFlip>
                <p /><p />
                <CardQS src={qos_png} alt="Queen of Spades" />
                <CardJD src={jod_png} alt="Jack of Diamonds" />
                {/* <img src={qos_png} alt="Queen of Spades" className="App-logo" />
                <img src={jod_png} alt="Jack of Diamonds" className="App-logo2" /> */}
                <p /><p />
                <HeadShake>(<i>Coming soon!</i>)</HeadShake>
                {/* <button style={{height: "30px", width: "100px", fontSize: "16px"}} onClick={this.start}>Play!</button> */}
            </div>
        );
    }

}

export default PinochleGame;